@use "global" as *;

.p-media {
  padding-top: myrem(14);
  padding-bottom: myrem(78);
  background-color: #fff;
  position: relative;
  @include mq(md){
    padding-top: myrem(48);
    padding-bottom: myrem(33);
  }
}

.p-media::after {
  content: "";
  width: 0;
  height: 0;
  display: block;
  margin: 0 auto;
  border-style: solid;
  border-width: myrem(25) myrem(24.5) 0 myrem(24.5);
  border-color: $white transparent transparent transparent;
  position: absolute;
  bottom: myrem(-25);
  left: 0;
  right: 0;
  z-index: 1;
  @include mq(md){
    border-width: myrem(41) myrem(65) 0 myrem(65);
    bottom: myrem(-41);
  }
}

.p-media__head {
  text-align: center;
}

.p-media__title {
  display: inline-block;
  font-size: myrem(24);
  color: $darkgreen1;
  line-height: calc(35 / 24);
  font-weight: 900;
  position: relative;
  @include mq(md){
    font-size: myrem(36);
    line-height: calc(36 / 36);
  }
}

.p-media__title::before {
  content: "";
  width: myrem(37);
  height: myrem(50);
  background: transparent url(../images/icon_title.svg) no-repeat center center;
  -webkit-background-size: 100%;
  background-size: 100%;
  position: absolute;
  top: myrem(17);
  left: myrem(-53);
  @include mq(md){
    top: myrem(2);
    left: myrem(-45);
  }
}

.p-media__title::after {
  content: "";
  width: myrem(37);
  height: myrem(50);
  background: transparent url(../images/icon_title.svg) no-repeat center center;
  -webkit-background-size: 100%;
  background-size: 100%;
  position: absolute;
  top: myrem(17);
  right: myrem(-53);
  transform: rotateY(180deg);
  @include mq(md){
    top: myrem(2);
    right: myrem(-43);
  }
}

.p-media__body {
  margin-top: myrem(15);
  @include mq(md){
    margin-top: myrem(38);
  }
}

.p-media__wrapper {
  width: calc(346 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  padding: myrem(20) 0 myrem(28);
  border: 5px solid $darkgreen1;
  box-sizing: border-box;
  border-radius: myrem(10);
  position: relative;
  @include mq(md){
    width: calc(948 / 1000 * 100%);
    padding: myrem(23) myrem(22) myrem(12) myrem(17);
    display: -webkit-box;
    display: flex;
    align-items: center;
  }
}

.p-media__img1 {
  width: 94%;
  margin-right: auto;
  margin-left: auto;
  @include mq(md){
    width: calc(591 / 899 * 100%);
    margin-left: 0;
  }
}

.p-media__img2 {
  width: calc(155 / 346 * 100%);
  position: absolute;
  bottom: myrem(-51);
  right: myrem(-5);
  @include mq(md){
    width: calc(285 / 899 * 100%);
    position: static;
  }

}


