@use "global" as *;

.p-companies {
  padding-top: myrem(25);
  padding-bottom: myrem(43);
  background: $lightgreen2 url(../images/bg_gray_sp.png) no-repeat center center;
  -webkit-background-size: cover;
  background-size: cover;
  @include mq(md){
    padding-top: myrem(66);
    padding-bottom: myrem(55);
    background: $lightgreen2;
  }
}

.p-companies__inner {
  width: calc(344 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  border: 5px solid $darkgreen1;
  box-sizing: border-box;
  background-color: $white;
  border-radius: myrem(30);
  background: $white url(../images/company_bg_sp.png) no-repeat center top;
  -webkit-background-size: 101% myrem(104);
  background-size: 101% myrem(68);
  @include mq(md){
    width: calc(913 / 1000 * 100%);
    border-radius: myrem(32) myrem(32) myrem(30) myrem(30);
    background: $white url(../images/company_bg_pc.png) no-repeat center top;
    -webkit-background-size: 101%;
    background-size: 101%;
  }
}

.p-companies__head {
  padding: myrem(11) 0 myrem(20) 0;
  // border: 1px solid $darkgreen1;
  @include mq(md){
    padding: myrem(22) 0 myrem(21) 0;
    // border-radius: myrem(24) myrem(24) 0 0;
    // background: $white url(../images/company_bg_pc.png) no-repeat center top;
    // -webkit-background-size: 101%;
    // background-size: 101%;
  }
}

.p-companies__title {
  font-size: myrem(24);
  color: $white;
  text-align: center;
  line-height: calc(35 / 24);
  font-weight: 900;
  @include mq(md){
    font-size: myrem(40);
    line-height: calc(58 / 40);
  }
}

.p-companies__body {
  padding: myrem(20) 0 myrem(40) 0;
  @include mq(md){
    padding: myrem(20) 0 myrem(53) 0;
  }
}

.p-companies__img {
  width: calc(272 / 334 * 100%);
  margin-left: auto;
  margin-right: auto;
  @include mq(md){
    width: calc(826 / 903 * 100%);
  }
}
