@use "global" as *;

.p-features {
  padding-top: myrem(19);
  background-color: $white;
  @include mq(md){
    padding-top: myrem(28);
  }
}

.p-features__head {
  margin-right: auto;
  margin-left: auto;
  @include mq(md){
    width: calc(960 / 1000 * 100%);
  }
}

.p-features__head dl {
  display: -webkit-box;
  display: flex;
  flex-direction: column;
  @include mq(md){
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border: 2px solid #00918C;
    box-sizing: border-box;
    background-color: $white;
  }
}

.p-features__head dl dt {
  width: calc(172 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  padding: myrem(9) 0 myrem(7) myrem(9);
  background-color: #00918C;
  position: relative;
  @include mq(md){
    width: calc(240 / 960 * 100%);
    margin-right: 0;
    margin-left: 0;
    padding: myrem(10) 0 myrem(8) myrem(12);
  }
}

.p-features__head dl dt::after {
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: myrem(13) myrem(9) 0 myrem(9);
  border-color: #02918c transparent transparent transparent;
  position: absolute;
  bottom: myrem(-12);
  left: 46%;
  transform: translate(-50%, 0);
  -webkit-transform: translate(-50%, 0);
  @include mq(md){
    border-width: myrem(19) myrem(13) 0 myrem(13);
    bottom: myrem(-18);
    left: 45%;
  }
}

.p-features__head dl dt span {
  width: calc(156 / 172 * 100%);
  display: block;
  @include mq(md){
    width: calc(218 / 240 * 100%);
  }
}

.p-features__head dl dd {
  width: calc(322 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  margin-top: myrem(22);
  color: $darkgreen3;
  font-size: myrem(14);
  text-align: center;
  line-height: calc(24 / 14);
  font-weight: 900;
  display: -webkit-box;
  display: flex;
  justify-content: center;
  align-items: center;
  @include mq(md){
    width: calc(720 / 960 * 100%);
    margin-right: 0;
    margin-left: 0;
    margin-top: 0;
    font-size: myrem(17);
    text-align: left;
    line-height: calc(26 / 17);
  }
}

.p-features__list {
  // width: calc(367 / 375 * 100%);
  margin-top: myrem(2);
  margin-right: auto;
  margin-left: auto;
  display: -webkit-box;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  @include mq(md){
    width: auto;
    max-width: myrem(967);
    margin-top: myrem(33);
  }
}

.p-features__item {
  width: calc(187 / 375 * 100%);
  @include mq(md){
    width: calc(322 / 967 * 100%);
  }
}

.p-features__item:nth-child(n+4) {
  @include mq(md){
    margin-top: myrem(5);
  }
}

