@use "global" as *;

.p-entry {
  padding: myrem(47) 0;
  background: #E2F4F2 url(../images/entry_bg_sp.png) no-repeat center center;
  -webkit-background-size: cover;
  background-size: cover;
  position: relative;
  @include mq(md){
    padding: myrem(92) 0 myrem(91) 0;
    background: #E2F4F2 url(../images/entry_bg_pc.png) no-repeat center center;
    -webkit-background-size: cover;
    background-size: cover;
  }
}

.p-entry.p-entry--last::after {
  content: "";
  width: 100%;
  height: myrem(59);
  display: block;
  background-color: $lightgreen;
  position: absolute;
  bottom: myrem(-59);
  left: 0;
}

