.p-entry__btn {
  width: calc(287 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  position: relative;
  @include mq(md){
    width: auto;
    max-width: myrem(558);
    height: myrem(97);
  }
}

.p-entry__btn__img {

}

.p-entry__btn__img img {
  width: 100%;
  aspect-ratio: 287 / 71;
  object-fit: contain;
  @include mq(md){
    aspect-ratio: 558 / 97;
  }
}

.p-entry__btn a {
  width: 100%;
  height: 100%;
  margin-top: myrem(-3);
  margin-left: myrem(-14);
  display: -webkit-box;
  display: flex;
  justify-content: center;
  align-items: center;
  color: $white;
  font-size: myrem(16);
  line-height: calc(24 / 16);
  font-weight: 900;
  position: absolute;
  top: 0;
  left: 0;
  @include mq(md){
    margin-top: 0;
    margin-left: 0;
    padding-left: myrem(117);
    font-size: myrem(26);
    line-height: calc(37 / 26);
    justify-content: flex-start;
  }
}


