@use "global" as *;

.p-step {
  padding-top: myrem(45);
  padding-bottom: myrem(20);
  background-color: $lightgreen2;
  position: relative;
  @include mq(md){
    padding-top: myrem(45);
    padding-bottom: myrem(58);
  }
}

.p-step::after {
  content: "";
  width: 0;
  height: 0;
  display: block;
  margin: 0 auto;
  border-style: solid;
  border-width: myrem(25) myrem(24.5) 0 myrem(24.5);
  border-color: $lightgreen2 transparent transparent transparent;
  position: absolute;
  bottom: myrem(-25);
  left: 0;
  right: 0;
  z-index: 1;
  @include mq(md){
    border-width: myrem(41) myrem(65) 0 myrem(65);
    bottom: myrem(-41);
  }
}

.p-step__inner {
  width: calc(334 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  @include mq(md){
    width: calc(970 / 1000 * 100%);
  }
}

.p-step__head {
  position: relative;
}

.p-step__head::after {
  content: "";
  width: myrem(96);
  height: 2px;
  background: transparent url(../images/step_border_sp.svg) no-repeat center center;
  -webkit-background-size: 100%;
  background-size: 100%;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  -webkit-transform: translate(0, -50%);
  @include mq(md){
    width: myrem(506);
    background: transparent url(../images/step_border.svg) no-repeat center center;
  }
}

.p-step__title {
  display: -webkit-box;
  display: flex;
  align-items: center;
  font-size: myrem(16);
  color: #009993;
  line-height: calc(24 / 16);
  font-weight: 900;
  @include mq(md){
    font-size: myrem(36);
    line-height: calc(52 / 36);
  }
}

.p-step__title span {
  margin-right: myrem(3);
  @include mq(md){
    margin-right: myrem(4);
  }
}

.p-step__wrapper {
  width: calc(297 / 334 * 100%);
  margin-right: auto;
  margin-left: auto;
  margin-top: myrem(14);
  @include mq(md){
    width: auto;
    margin-right: 0;
    margin-left: 0;
    margin-top: myrem(24);
  }
}

