@use "global" as *;

.p-reason {
  width: calc(345 / 375 * 100%);
  margin-top: myrem(27);
  margin-right: auto;
  margin-left: auto;
  border: 3px solid $darkgreen3;
  box-sizing: border-box;
  border-radius: myrem(10);
  background-color: $white;
  @include mq(md){
    width: calc(970 / 1000 * 100%);
    margin-top: myrem(43);
    border: 5px solid $darkgreen3;
  }
}

.p-reason__inner {
  padding-top: myrem(28);
  padding-bottom: myrem(38);
  @include mq(md){
    margin-right: myrem(15);
    margin-left: myrem(15);
    padding-top: myrem(27);
    padding-bottom: myrem(42);
  }
}

.p-reason__head {
  position: relative;
}

.p-reason__head::after {
  @include mq(md){
    content: "";
    width: myrem(290);
    height: 2px;
    background: transparent url(../images/reason_border.svg) no-repeat center center;
    -webkit-background-size: 100%;
    background-size: 100%;
    position: absolute;
    top: myrem(31);
    right: 0;
  }
}

.p-reason__title {
  width: calc(305 / 339 * 100%);
  margin-right: auto;
  margin-left: myrem(13);
  display: -webkit-box;
  display: flex;
  align-items: center;
  color: #111;
  font-size: myrem(18);
  line-height: calc(26 / 18);
  font-weight: 900;
  @include mq(md){
    width: myrem(628);
    margin-left: myrem(11);
    margin-right: 0;
    font-size: myrem(36);
    line-height: calc(52 / 36);
  }
}

.p-reason__title span {
  width: myrem(122);
  margin-right: myrem(3);
  @include mq(md){
    width: myrem(264);
    margin-right: myrem(4);
  }
}

.p-reason__read {
  width: calc(313 / 339 * 100%);
  margin-top: myrem(13);
  margin-right: auto;
  margin-left: auto;
  font-size: myrem(12);
  color: #009A94;
  line-height: calc(17 / 12);
  font-weight: 900;
  @include mq(md){
