@use "global" as *;

.p-point__title {
  width: calc(345 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  @include mq(md){
    width: calc(986 / 1000 * 100%);
  }
}

.p-point__wrapper {
  width: calc(344 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  margin-top: myrem(15);
  @include mq(md){
    width: calc(969 / 1000 * 100%);
    margin-top: myrem(15);
  }
}

.p-point__item {
  padding: myrem(10) myrem(10);
  background-color: #DF682C;
  border-radius: myrem(10);
  display: -webkit-box;
  display: flex;
  flex-direction: column;
  @include mq(md){
    padding: myrem(19) myrem(15) myrem(19) myrem(18);
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.p-point__item:nth-child(n+2) {
  margin-top: myrem(10);
  @include mq(md){
    margin-top: myrem(18);
  }
}

.p-point__head {
  padding: myrem(6) myrem(10) myrem(9);
  display: -webkit-box;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  background-color: $white;
  border-radius: myrem(10);
  @include mq(md){
    width: calc(583 / 932 * 100%);
    min-height: myrem(104);
    padding: myrem(25) myrem(12);
    flex-wrap: nowrap;
  }
}

.p-point__icon {
  width: myrem(51);
  margin-right: myrem(15);
  @include mq(md){
    width: calc(63 / 559 * 100%);
    margin-right: myrem(13);
  }
}

.p-point__icon img {
  width: 100%;
  aspect-ratio: 51 / 45;
  object-fit: contain;
  @include mq(md){
    aspect-ratio: 63 / 55;
  }
}

.p-point__sub-title {
  font-size: myrem(24);
  color: #DF682C;
  line-height: calc(35 / 24);
  letter-spacing: 0;
  font-weight: 900;
  @include mq(md){
    margin-right: myrem(26);
    font-size: myrem(38);
    line-height: calc(55 / 38);
  }
}

.p-point__read {
  width: 100%;
  margin-top: myrem(4);
  font-size: myrem(10);
  line-height: calc(15 / 10);
  font-weight: 500;
  @include mq(md){
    width: auto;
    margin-top: 0;
    font-size: myrem(15);
    line-height: calc(21 / 15);
    
  }
}

.p-point__body {
  padding: 0 myrem(11);
  @include mq(md){
    width: calc(334 / 932 * 100%);
    padding: 0;
  }
}

.p-point__text {
  margin-top: myrem(8);
  font-size: myrem(12);
  color: $white;
  line-height: calc(17 / 12);
  font-weight: 500;
  @include mq(md){
    margin-top: 0;
    font-size: myrem(16);
    line-height: calc(24 / 16);
  }
}



