@use "global" as *;

.p-footer {
  padding: myrem(17) 0;
  background-color: #009A93;
  @include mq(md){
    padding: myrem(33) 0 myrem(32);
  }
}

.p-footer__copy {
  font-size: myrem(12);
  color: $white;
  text-align: center;
  line-height: calc(17 / 12);
  font-weight: bold;
  @include mq(md){
    font-size: myrem(16);
    line-height: calc(24 / 16);
  }
}


