@use "global" as *;

.p-career {
  padding-top: myrem(16);
  padding-bottom: myrem(17);
  background-color: $lightgreen2;
  position: relative;
  // overflow: hidden;
  @include mq(md){
    padding-top: myrem(26);
    padding-bottom: myrem(19);
  }
}

.p-career__inner {
  width: calc(354 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  padding-top: myrem(23);
  padding-bottom: myrem(43);
  background-color: $white;
  @include mq(md){
    width: calc(954 / 1000 * 100%);
    padding-top: myrem(41);
    padding-bottom: 0;
  }
}

.p-career__head {
  width: calc(325 / 354 * 100%);
  margin-right: auto;
  margin-left: auto;
  display: -webkit-box;
  display: flex;
  justify-content: center;
  position: relative;
  @include mq(md){
    width: auto;
    margin-right: myrem(19);
    margin-left: myrem(12);
    display: block;
  }
}

.p-career__head::after {
  @include mq(md){
    content: "";
    display: block;
    width: myrem(317);
    height: 2px;
    background: transparent url(../images/service_border.svg) no-repeat center center;
    -webkit-background-size: 100%;
    background-size: 100%;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    -webkit-transform: translate(0, -50%);
  }
}

.p-career__title {
  color: #009993;
  font-size: myrem(20);
  line-height: calc(29 / 20);
  font-weight: 900;
  display: -webkit-box;
  display: flex;
  align-items: center;
  @include mq(md){
    margin-right: auto;
    font-size: myrem(36);
    line-height: calc(52 / 36);
  }
}

.p-career__title span {
  width: myrem(140);
  margin-right: myrem(3);
  @include mq(md){
    width: myrem(264);
    margin-right: myrem(4);
  }
}

.p-career__read {
  width: calc(339 / 354 * 100%);
  margin-top: myrem(14);
  margin-right: auto;
  margin-left: auto;
  color: $darkgreen3;
  font-size: myrem(12);
  line-height: calc(20 / 12);
  font-weight: 900;
  @include mq(md){
    width: calc(848 / 954 * 100%);
    margin-top: myrem(15);
    font-size: myrem(18);
    line-height: calc(26 / 18);
  }
}

.p-career__wrap {
  margin-top: myrem(24);
  position: relative;
  @include mq(md){
    width: calc(936 / 954 * 100%);
    margin-right: auto;
    margin-left: auto;
    margin-top: myrem(37);
  }
}

.p-career__img-text {
  width: calc(278 / 355 * 100%);
  margin-right: auto;
  margin-left: myrem(15);
  margin-bottom: myrem(6);
  @include mq(md){
    display: none;
  }
}

.p-career__img {
  margin-left: myrem(7);
  position: relative;
  overflow-x: scroll;
  @include mq(md){
    margin-left: 0;
    overflow-x: visible;
  }
}

.p-career__img img.career {
  width: myrem(545);
  max-width: none;
  @include mq(md){
    width: 100%;
    max-width: 100%;
  }
}

.p-career__arrow {
  display: block;
  width: calc(121 / 347 * 100%);
  margin-top: myrem(48);
  margin-right: auto;
  margin-left: auto;
  @include mq(md){
    width: calc(165 / 885 * 100%);
    margin-top: myrem(-13);
  }
}

.p-career__text {
  width: calc(321 / 347 * 100%);
  margin-top: myrem(30);
  margin-right: auto;
  margin-left: auto;
  font-size: myrem(16);
  color: $darkgreen1;
  text-align: center;
  line-height: calc(24 / 16);
  font-weight: 900;
  @include mq(md){
    width: 100%;
    width: calc(855 / 885 * 100%);
    margin-top: myrem(14);
    margin-right: auto;
    margin-left: auto;
    font-size: myrem(26);
    line-height: calc(37 / 26);
  }
}

