@use "global" as *;

.l-bg01 {
  padding-top: myrem(32);
  padding-bottom: myrem(24);
  background: transparent url(../images/bg_gray_sp.png) repeat-y center center;
  -webkit-background-size: cover;
  background-size: cover;
  position: relative;
  @include mq(md){
    padding-top: myrem(33);
    padding-bottom: myrem(65);
    background: transparent url(../images/bg_gray_pc.png) no-repeat center center;
    -webkit-background-size: cover;
    background-size: cover;
  }
}

.l-bg01::after {
  content: "";
  width: myrem(40);
  height: myrem(21);
  display: block;
  background: transparent url(../images/arrow_gray_sp.svg) no-repeat center center;
  -webkit-background-size: 100%;
  background-size: 100%;
  position: absolute;
  bottom: myrem(-20);
  left: 50%;
  transform: translate(-50%, 0);
  -webkit-transform: translate(-50%, 0);
  z-index: 1;
  @include mq(md){
    width: myrem(130);
    height: myrem(41);
    background: transparent url(../images/arrow_gray_pc.svg) no-repeat center center;
    position: absolute;
    bottom: myrem(-40);
  }
}
