@use "global" as *;

.p-manegement {
  padding-top: myrem(26);
  padding-bottom: myrem(20);
  background-color: $lightgreen2;
  position: relative;
  @include mq(md){
    padding-top: myrem(110);
    padding-bottom: myrem(94);
  }
}

.p-manegement__title {
  font-size: myrem(24);
  color: #009A93;
  text-align: center;
  line-height: calc(35 / 24);
  font-weight: 900;
  position: relative;
  z-index: 1;
  @include mq(md){
    padding-bottom: myrem(27);
    font-size: myrem(40);
    line-height: calc(58 / 40);
  }
}

.p-manegement__title::after {
  content: attr(data-en);
  font-size: myrem(54);
  color: rgba(136, 136, 136 , 0.2);
  text-align: center;
  line-height: calc(79 / 54);
  letter-spacing: -0.03em;
  font-weight: 900;
  position: absolute;
  bottom: myrem(-16);
  left: 50%;
  transform: translate(-50%, 0);
  -webkit-transform: translate(-50%, 0);
  z-index: -1;
  @include mq(md){
    font-size: myrem(145);
    line-height: calc(174 / 120);
    bottom: myrem(-42);
  }
}

.p-manegement__body {
  padding-bottom: myrem(30);
  background-color: $white;
  @include mq(md){
    padding-top: myrem(33);
    padding-bottom: myrem(23);
  }
}

.p-manegement__list {
  width: calc(362 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  @include mq(md){
    width: calc(638 / 1000 * 100%);
  }
}

.p-manegement__item:nth-child(n+2) {
  margin-top: myrem(2);
}

.p-manegement__list dl {
  display: -webkit-box;
  display: flex;
  justify-content: space-between;
}

.p-manegement__list dl dt {
  width: calc(95 / 362 * 100%);
  padding: myrem(18) 0 myrem(16);
  background-color: rgba(255, 161, 0, 0.37);
  display: -webkit-box;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: myrem(14);
  line-height: calc(20 / 14);
  font-weight: bold;
  @include mq(md){
    width: calc(153 / 638 * 100%);
    padding: myrem(15) 0;
    font-size: myrem(16);
    line-height: calc(24 / 16);

  }
}

.p-manegement__list dl dd {
  width: calc(263 / 362 * 100%);
  padding: myrem(12) myrem(18) myrem(16) myrem(30);
  background-color: rgba(255, 161, 0, 0.09);
  font-size: myrem(12);
  line-height: calc(17 / 12);
  font-weight: 500;
  display: -webkit-box;
  display: flex;
  align-items: center;
  @include mq(md){
    width: calc(485 / 638 * 100%);
    min-height: myrem(54);
    padding: myrem(9) myrem(51);
    font-size: myrem(16);
    line-height: calc(24 / 16);
  }
}

