@use "global" as *;

.p-interview {
  padding-top: myrem(20);
  padding-bottom: myrem(60);
  background-color: #EDF5E4;
  @include mq(md){
    padding-top: myrem(56);
    padding-bottom: myrem(66);
  }
}

.p-interview__inner {
  margin-right: auto;
  margin-left: auto;
  @include mq(md){
    width: calc(925 / 1000 * 100%);
  }
}

.p-interview__head {
  width: calc(336 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  @include mq(md){
    width: auto;
    margin-right: 0;
    margin-left: 0;
    flex-wrap: nowrap;
  }
}

.p-interview__icon {
  width: calc(30 / 336 * 100%);
  margin-right: myrem(3);
  height: myrem(73);
  @include mq(md){
    width: myrem(83);
    margin-right: myrem(11);
    height: auto;
  }
}

.p-interview__title {
  width: calc(144 / 336 * 100%);
  font-size: myrem(16);
  color: $darkgreen3;
  line-height: calc(24 / 16);
  font-weight: 900;
  @include mq(md){
    width: auto;
    font-size: myrem(32);
    line-height: calc(46 / 32);
  }
}

.p-interview__title span {
  width: 100%;
  font-size: myrem(12);
  color: #111;
  line-height: calc(18 / 12);
  font-weight: bold;
  position: absolute;
  bottom: 0;
  left: 0;
  @include mq(md){
    display: block;
    font-size: myrem(16);
    line-height: calc(24 / 16);
    position: static;
  }
}

.p-interview__list {
  margin-top: myrem(14);
  width: calc(307 / 375 * 100%);
  margin-right: auto;
  margin-left: auto;
  @include mq(md){
    width: auto;
    margin-right: 0;
    margin-left: 0;
    margin-top: myrem(29);
    display: -webkit-box;
    display: flex;
    justify-content: space-between;
  }
}

.p-interview__item {
  margin: 0 myrem(5) myrem(10);
  padding: myrem(12) myrem(12) myrem(38);
  background-color: $white;
  border-radius: myrem(10);
  box-shadow: 0 myrem(3) myrem(6) rgba(0, 0, 0, 0.16);
  @include mq(md){
    width: calc(297 / 925 * 100%);
    margin: 0;
    padding: myrem(12) myrem(12) myrem(33);
  }
}

.p-interview__item-top {
  display: -webkit-box;
  display: flex;
  align-items: center;
}

.p-interview__item-img {
  width: myrem(70);
  margin-right: myrem(23);
  @include mq(md){
    width: myrem(78);
    margin-right: myrem(11);
  }
}

.p-interview__item-name {
  font-size: myrem(16);
  line-height: calc(24 / 16);
  font-weight: bold;
  @include mq(md){
    font-size: myrem(18);
    line-height: calc(26 / 18);
  }
}

.p-interview__item-text {
  margin-top: myrem(23);
  padding: 0 myrem(4);
  font-size: myrem(14);
  line-height: calc(20 / 14);
  font-weight: 500;
  @include mq(md){
    margin-top: myrem(16);
    padding: 0;
    font-size: myrem(16);
    line-height: calc(24 / 16);
  }
}





/****** ul.slider ******/
.js-interview-slider.slick-dotted.slick-slider {
  margin-bottom: 0;
}

.js-interview-slider .prev-arrow,
.js-interview-slider .next-arrow {
  width: myrem(20);
  height: myrem(30);
  display: block;
  cursor: pointer;
  background: transparent url(../images/interview_arrow.png) no-repeat center center;
  -webkit-background-size: 100%;
  background-size: 100%;
  position: absolute;
  top: 46%;
  z-index: 1;
}

.js-interview-slider .prevArrow {
  left: myrem(-30);
  transform: rotateY(180deg) translate(0, -50%);
  -webkit-transform: rotateY(180deg) translate(0, -50%);
}

.js-interview-slider .next-arrow {
  right: myrem(-30);
  transform: translate(0, -50%);
  -webkit-transform: translate(0, -50%);
}


/****** slick-dots ******/
.js-interview-slider .slick-dots {
  height: 10px;
  position: absolute;
  bottom: myrem(-22);
  left: myrem(-10);
}

.js-interview-slider .slick-dots li {
  width: 10px;
  height: 10px;
  margin: 0 5px !important;
}

.js-interview-slider .slick-dots li button {
  width: 10px;
  height: 10px;
  padding: 0;
}

.js-interview-slider .slick-dots li button:before {
  width: 10px;
  height: 10px;
  display: block;
  font-size: 70px;
  font-size: 20px;
  color: #C7C7C7;
  line-height: 0;
  position: relative;
  opacity: 1;
}

.js-interview-slider .slick-dots li.slick-active button:before {
  color: #00ACA4;
}